.App {
  padding: 30px;
  display: flex;
}

.App > div {
  margin-right: 50px;
  min-width: 200px;
}

button {
  margin: 10px;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}

.title-button {
  display: flex;
  justify-content: space-between;
}

.error {
  border: red 1px solid;
}

.error-message {
  color: red;
}

.active-filter {
  background-color: aquamarine;
}

.is-done {
  text-decoration: line-through;
}

.block {
  border: black 1px solid;
  padding: 10px;
  box-sizing: border-box;
  background-color: blanchedalmond;
  border-radius: 30px;
}